/**
 * Detecta se a aplicao est rodando dentro de um app nativo (webview)
 * Retorna false para uso em browsers web, mesmo em dispositivos mveis
 * S retorna true quando executado dentro do app nativo
 */
export function isRunningInApp(): boolean {
  // Verifica se existe um user agent customizado do app
  const userAgent = navigator.userAgent.toLowerCase();
  
  // Verifica se h indicadores especficos do app nativo
  // Adicione aqui os identificadores nicos do seu app
  const appIdentifiers = [
    'xadrezapp', // exemplo de identificador customizado
    'acessototalapp', // outro exemplo
  ];
  
  // Verifica se algum identificador do app est presente no user agent
  const hasAppIdentifier = appIdentifiers.some(identifier => 
    userAgent.includes(identifier)
  );
  
  // Verifica se h propriedades especficas do app nativo no window
  const hasAppInterface = (
    typeof (window as any).ReactNativeWebView !== 'undefined' ||
    typeof (window as any).webkit?.messageHandlers !== 'undefined' ||
    typeof (window as any).Android !== 'undefined'
  );
  
  // Verifica se h parmetros de URL especficos do app
  const urlParams = new URLSearchParams(window.location.search);
  const hasAppParam = urlParams.get('app') === 'true' || urlParams.get('native') === 'true';
  
  return hasAppIdentifier || hasAppInterface || hasAppParam;
}

/**
 * Detecta se est rodando em um dispositivo mvel (mas no necessariamente no app)
 */
export function isMobileDevice(): boolean {
  return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
}

/**
 * Detecta se est rodando em um browser web (desktop ou mvel)
 */
export function isRunningInBrowser(): boolean {
  return !isRunningInApp();
}