import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { User } from '../domain/models/user';
import { getCurrentUser, saveUser, clearUser, getCurrentUserProfile, getAuthToken } from '../services/auth-service';

interface AuthContextType {
  user: User | null;
  isLoading: boolean;
  login: (user: User) => void;
  logout: () => void;
  isAuthenticated: boolean;
  refreshUserProfile: () => Promise<void>;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

interface AuthProviderProps {
  children: ReactNode;
}

export const AuthProvider: React.FC<AuthProviderProps> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null); // Inicializar como null, ser definido no useEffect
  const [isLoading, setIsLoading] = useState(true); // Inicializar como true para mostrar loading

  // Buscar perfil atualizado do backend no reload da aplicao
  useEffect(() => {
    const initializeUser = async () => {
      const token = getAuthToken();
      const currentSavedUser = getCurrentUser(); // Buscar usurio salvo dentro do useEffect
      
      console.log(" Inicializando AuthContext:");
      console.log(" Token encontrado:", !!token);
      console.log(" Usurio salvo encontrado:", !!currentSavedUser);
      console.log(" Dados do usurio salvo:", currentSavedUser ? { id: currentSavedUser.id, name: currentSavedUser.name, email: currentSavedUser.email } : "Nenhum");
      
      try {
        // Se h token, buscar dados atualizados
        if (token) {
          console.log(" Token encontrado - inicializando usurio");
          
          // Sempre carregar dados locais primeiro (se existirem)
          if (currentSavedUser) {
            console.log(" Carregando dados locais do usurio");
            setUser(currentSavedUser);
            
            // Para tokens no-mock, buscar dados atualizados do backend
            if (!token.startsWith('mock_token_')) {
              try {
                console.log(" Buscando dados atualizados do backend");
                const profile = await getCurrentUserProfile();
                
                if (profile) {
                  const updatedUser: User = {
                    id: profile.id,
                    username: currentSavedUser.username || profile.email,
                    email: profile.email,
                    name: profile.name,
                    role: currentSavedUser.role || 'user',
                    hotmartId: profile.hotmartId,
                    courseAccess: currentSavedUser.courseAccess ?? true,
                    createdAt: profile.createdAt,
                    updatedAt: profile.updatedAt,
                    profileImage: profile.profileImage,
                  };
                  
                  console.log(" Dados do usurio atualizados com sucesso");
                  setUser(updatedUser);
                  saveUser(updatedUser);
                } else {
                  console.log(" No foi possvel buscar perfil - mantendo dados locais");
                }
              } catch (profileError) {
                console.warn(" Erro ao buscar perfil - mantendo dados locais:", profileError);
              }
            } else {
              console.log(" Token mock detectado - usando dados locais");
            }
          } else {
            // Sem dados locais, tentar buscar do backend (apenas para tokens reais)
            if (!token.startsWith('mock_token_')) {
              console.log(" Sem dados locais - buscando do backend");
              try {
                const profile = await getCurrentUserProfile();
                
                if (profile) {
                  const newUser: User = {
                    id: profile.id,
                    username: profile.email,
                    email: profile.email,
                    name: profile.name,
                    role: 'user',
                    hotmartId: profile.hotmartId,
                    courseAccess: true,
                    createdAt: profile.createdAt,
                    updatedAt: profile.updatedAt,
                    profileImage: profile.profileImage,
                  };
                  
                  console.log(" Usurio criado a partir do backend");
                  setUser(newUser);
                  saveUser(newUser);
                } else {
                  console.log(" No foi possvel obter perfil do backend");
                  clearUser();
                  setUser(null);
                }
              } catch (error) {
                console.error(" Erro ao buscar perfil:", error);
                clearUser();
                setUser(null);
              }
            } else {
              // Token mock sem dados locais - fazer logout
              console.log(" Token mock sem dados locais - fazendo logout");
              clearUser();
              setUser(null);
            }
          }
        } else if (currentSavedUser) {
          // Se no h token mas h usurio salvo, limpar dados inconsistentes
          console.log(" Usurio salvo sem token - limpando dados");
          clearUser();
          setUser(null);
        } else {
          // Nenhum token nem usurio salvo - usurio no logado
          console.log(" Nenhum usurio logado");
          setUser(null);
        }
      } catch (error) {
        console.error(" Erro ao inicializar usurio:", error);
        // Manter usurio salvo em caso de erro
        if (currentSavedUser) {
          console.log(" Mantendo dados do usurio salvos localmente devido ao erro");
          setUser(currentSavedUser);
        } else {
          clearUser();
          setUser(null);
        }
      } finally {
        setIsLoading(false);
        console.log(" Inicializao do AuthContext finalizada");
      }
    };

    initializeUser();
  }, []);

  const login = (userData: User) => {
    setUser(userData);
    saveUser(userData);
  };

  const logout = () => {
    setUser(null);
    clearUser();
  };

  const refreshUserProfile = async () => {
    try {
      const profile = await getCurrentUserProfile();
      if (profile && user) {
        const updatedUser: User = {
          ...user,
          id: profile.id,
          hotmartId: profile.hotmartId,
          email: profile.email,
          name: profile.name,
          createdAt: profile.createdAt,
          updatedAt: profile.updatedAt,
          profileImage: profile.profileImage,
          courseAccess: true,
        };
        setUser(updatedUser);
        saveUser(updatedUser);
      }
    } catch (error) {
      console.error("Erro ao atualizar perfil:", error);
      // No fazer logout em caso de erro
    }
  };

  const value: AuthContextType = {
    user,
    isLoading,
    login,
    logout,
    isAuthenticated: !!user,
    refreshUserProfile,
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = (): AuthContextType => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};